import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import { apiGet } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import { Link } from "react-router-dom";

function DriveTestPackage({ introData, imageSrc, buttonClass, themeColor }) {
  const [contentData, setContentData] = useState(null);
  const [error, setError] = useState(null); // State to handle errors

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await apiGet(API_ENDPOINTS.TEST_PACKAGE);
        if (!response || !response.data) {
          throw new Error("Invalid response format for testPackage data");
        }
        const data = response.data[0]; // Assuming it's an array and taking the first item
        setContentData({
          paragraph: data.listing,
          price: `$${data.price}`,
          disclaimer: data.disclaimer,
          buttonName: "Book a Test Package Now!",
        });
      } catch (err) {
        setError(err.message);
        console.error("Error fetching test package data:", err);
      }
    };

    fetchData();
  }, []);

  if (error) {
    return <div className="alert alert-danger">{error}</div>; // Display error message
  }

  return (
    <div className="DriveTest" style={{ backgroundColor: themeColor }}>
      <Container>
        <Row>
          <Col lg={12}>
            {/* Section intro block */}
            <SecIntro
              titleStart={introData.titleStart}
              highlight={introData.highlight}
              titleEnd={introData.titleEnd}
              theme={introData.theme}
            />
            <div className="DriveTest__wrp">
              <div className="DriveTest__left">
                <div className="d-flex flex-column">
                  <div
                    className="DriveTest__content"
                    dangerouslySetInnerHTML={{
                      __html: contentData?.paragraph || "Loading data...",
                    }}
                  />
                  <div className="price">
                    <h4>{contentData ? contentData.price : ""}</h4>
                  </div>
                  <Link to="/book-now" className={`btn ${buttonClass}`}>
                    {contentData?.buttonName || "Book a Test Package Now!"}
                  </Link>
                  {contentData?.disclaimer && (
                    <div
                      className="disclaimer"
                      dangerouslySetInnerHTML={{ __html: contentData?.disclaimer || "Loading data...", }}
                    />
                  )}
                </div>
              </div>
              <div className="DriveTest__right">
                <img src={imageSrc} alt="Driving Test" />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default DriveTestPackage;